const myMenu = config.menuData;
class SliderToolBar extends React.PureComponent {
	state = {
		inputValue: 15,
	};
	onChange = (value) => {
		this.setState({
			inputValue: value,
		});
		const saveData = this.props.getTemporaryStorage();
		if (zTool.dataType.isObject(saveData)) {
			saveData.doc && saveData.doc.setFontSize(value + "px");
			this.props.setTemporaryStorage({
				...saveData,
				sliderValue: value + "px",
			});
		}
	};
	render() {
		return (
			<div style={{ width: "300px", height: "100%", marginRight: "100px" }} className="z-padding-top-12 z-flex-items-v-center">
				<span>字体大小</span>
				<div className="z-flex-1">
					<Slider min={12} max={50} onChange={this.onChange} value={this.state.inputValue} style={{margin:"10px 6px"}}/>
				</div>
			</div>
		);
	}
}
SliderToolBar = ZerodMainContext.setConsumer(SliderToolBar);
